.searchheading{
    text-align: left;
    color: blue;
    font-size:25px;
    line-height:0.3;
    font-weight: bolder;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.searchsubheading{
    text-align: left;
    color:grey;
    font-weight: bolder;
    line-height:1.9;
    letter-spacing: normal;
    font-size:18px;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.boxcontainer{
    box-shadow:0 3px 6px;
    margin-right: 30px;
    margin-bottom: 30px;
    margin-top:30px ;
    width:300px;
    background-image:white;
    display:inline-block;
}
.boxcomponent1{
    width:50%;
    float: left;
}
.boxcomponent1>img{
    height:120px;
    width: 120px;

}
.boxcomponent2{
    width:50%;
    float: left;
    margin-top: 20px;
}
.boxcomponentheading{
    text-align: left;
    line-height:1.0;
    color: blue;
    font-size:18px;
    font-weight: bold;
}
.boxcomponentsubheading{
    text-align: left;
    line-height:1.0;
    color:gray;
    font-size:14px;
    font-weight: bold;
}